@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin constentPaddinInline() {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 34px;
  }

  @include onDesktop {
    padding-inline: 55px;
  }
}

@mixin transition() {
  transition: all 500ms;
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 30px;
  }

  @include onDesktop {
    --columns: 12;
  }
}
